<template>
   <div>
      <v-app id="aspire">
         <v-container>

            <v-card>
               <v-card-text class="mt-2" style="font-size: 19px;
               font-weight: bold;">Form </v-card-text>
               <v-col cols="12" md="12">
                  <v-row dense>
                     <v-col cols="12" md="6">
                        <v-row dense>
                           <v-col cols="12" md="3">
                              <label>Name</label>
                           </v-col>
                           <v-col cols="12" md="5">
                              <v-text-field single-line outlined dense></v-text-field>
                           </v-col>
                        </v-row>
                        <v-row dense>
                           <v-col cols="12" md="3">
                              <label>Father Name</label>
                           </v-col>
                           <v-col cols="12" md="5">
                              <v-text-field outlined single-line dense></v-text-field>
                           </v-col>
                        </v-row>
                        <v-row dense>
                           <v-col cols="12" md="3">
                              <label>Birth Date</label>
                           </v-col>
                           <v-col  cols="12" md="5">
                           <v-menu
                              ref="menu1"
                              v-model="menu1"
                              :close-on-content-click="false"
                              transition="scale-transition"
                              offset-y
                              max-width="290px"
                              min-width="auto"
                           >
                              <template v-slot:activator="{ on, attrs }">
                                 <v-text-field
                                 v-model="dateFormatted"
                                 hint="MM/DD/YYYY format"
                                 persistent-hint
                                 append-icon="mdi-calendar"
                                 v-bind="attrs"
                                 @blur="date = parseDate(dateFormatted)"
                                 v-on="on"
                                 outlined dense
                                 single-line
                                 ></v-text-field>
                              </template>
                              <v-date-picker
                                 v-model="date"
                                 no-title
                                 @input="menu1 = false"
                              ></v-date-picker>
                           </v-menu>
                           </v-col>
                        </v-row>
                        <v-row dense>
                           <v-col cols="12" md="3">
                              <label>Address</label>
                           </v-col>
                           <v-col cols="12" md="5">
                              <v-text-field outlined dense single-line></v-text-field>
                           </v-col>
                        </v-row>
                     </v-col>
                     <v-col cols="12" md="6">
                        <v-row dense>
                           <v-col cols="12" md="3">
                              <label>Name</label>
                           </v-col>
                           <v-col cols="12" md="5">
                              <v-text-field outlined dense single-line></v-text-field>
                           </v-col>
                        </v-row>
                        <v-row dense>
                           <v-col cols="12" md="3">
                              <label>Father Name</label>
                           </v-col>
                           <v-col cols="12" md="5">
                              <v-text-field outlined single-line dense></v-text-field>
                           </v-col>
                        </v-row>
                        <v-row dense>
                           <v-col cols="12" md="3">
                              <label>Birth Date</label>
                           </v-col>
                           <v-col  cols="12" md="5">
                           <v-menu
                              ref="menu1"
                              v-model="menu1"
                              :close-on-content-click="false"
                              transition="scale-transition"
                              offset-y
                              max-width="290px"
                              min-width="auto"
                           >
                              <template v-slot:activator="{ on, attrs }">
                                 <v-text-field
                                 v-model="dateFormatted"
                                 hint="MM/DD/YYYY format"
                                 persistent-hint
                                 append-icon="mdi-calendar"
                                 v-bind="attrs"
                                 @blur="date = parseDate(dateFormatted)"
                                 v-on="on"
                                 outlined dense
                                 single-line
                                 ></v-text-field>
                              </template>
                              <v-date-picker
                                 v-model="date"
                                 no-title
                                 @input="menu1 = false"
                              ></v-date-picker>
                           </v-menu>
                           </v-col>
                        </v-row>
                        <v-row dense>
                           <v-col cols="12" md="3">
                              <label>Address</label>
                           </v-col>
                           <v-col cols="12" md="5">
                              <v-text-field outlined dense single-line></v-text-field>
                           </v-col>
                        </v-row>
                     </v-col>
                     <v-col cols="12" md="10" class="text-right">
                        <v-btn small class="mr-2" outlined color="warning">
                           <v-icon dark left>mdi-format-clear</v-icon>Cancel
                        </v-btn>
                        <v-btn small class="mr-2" outlined color="red">
                           <v-icon dark left>mdi-delete</v-icon>Delete
                        </v-btn>
                     </v-col>
                  </v-row>
               </v-col>
            </v-card>

         </v-container>
      </v-app>
   </div>
</template>


<script>
//eslint-disable-next-line
/* eslint-disable */
//import { ValidationProvider } from 'vee-validate';
//import { required } from 'vee-validate/dist/rules';


export default {
   data: vm => ({
      date: (new Date(Date.now() - (new Date()).getTimezoneOffset() * 60000)).toISOString().substr(0, 10),
      dateFormatted: vm.formatDate((new Date(Date.now() - (new Date()).getTimezoneOffset() * 60000)).toISOString().substr(0, 10)),
      menu1: false,
      menu2: false,
   }),

   computed: {
      computedDateFormatted () {
      return this.formatDate(this.date)
      },
   },

   watch: {
      date (val) {
      this.dateFormatted = this.formatDate(this.date)
      },
   },

   methods: {
      formatDate (date) {
      if (!date) return null

      const [year, month, day] = date.split('-')
      return `${month}/${day}/${year}`
      },
         parseDate (date) {
      if (!date) return null

      const [month, day, year] = date.split('/')
      return `${year}-${month.padStart(2, '0')}-${day.padStart(2, '0')}`
      },
   },
}

</script>
