import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import About from '../views/About'
import Login from '../views/login'
import EntryForm from  '../views/entryform'
import Datatabele from '@/views/datatabele'
import VelidateEntryForm from '../views/Components/velidateentryform'
import Popover from '@/views/Components/Popover'
import Chart from '../views/Components/Chart'
import Banner from '../views/Components/Banner'
//import NavigationDrawer from '../views/NavigationDrawer'
Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Login',
    component: Login
  },
  {
    path: '/home',
    name: 'Home',
    component: Home
    },
    {
      path:'/chart',
      name:'Chart',
      component:Chart
    },
    {
      path:'/banner',
      name:'Banner',
      component:Banner
    },
    {
      path: '/datatable',
      name: 'Datatabele',
      component: Datatabele
    },
  {
    path:'/entryform',
    name: "EntryForm",
    component: EntryForm
  },
  {
    path: '/about',
    name: 'About',
    component: About,
  },
  {
    path: '/velidateentryform',
    name: 'VelidateEntryForm',
    component: VelidateEntryForm,
  },
  {
    path: '/popover',
    name:'Popover',
    component: Popover,
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
